import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NewEmployeesPageComponent } from './components/new-employees-page/new-employees-page.component';
import { NewEmployeesPageResolver } from 'src/app/services/employees/new-employees-page.resolver';

const routes: Routes = [
  {
    path: '',
    component: NewEmployeesPageComponent,
    resolve: { newEmployees: NewEmployeesPageResolver },
    data: { animation: 'fadeAnimation', title: 'Новые сотрудники' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [NewEmployeesPageResolver],
    exports: [RouterModule]
})
export class NewEmployeesRoutingModule { }
