import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import * as moment from 'moment';
import * as _ from 'lodash';

import { Employee } from 'src/app/services/employees/employee';
import { IListItemsPage } from 'src/app/services/list-items-service';
import { EmployeesService } from 'src/app/services/employees/employees.service';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';

export interface Birthday {
  birthday: string;
  birthmonth: string;
  employees: Employee[];
}

@Component({
  selector: 'app-new-employees-page',
  templateUrl: './new-employees-page.component.html',
  styleUrls: ['./new-employees-page.component.scss']
})
export class NewEmployeesPageComponent implements OnInit {
  private pages: IListItemsPage<Employee>[] = [];
  private index = 0;
  public employees: Employee[] = [];
  public birthdays: Birthday[] = [];
  public hasPrev = false;
  public hasNext = false;
  public month: number;
  public quater: number;
  public months: string[];

  constructor(
    private route: ActivatedRoute,
    private employeesService: EmployeesService,
    private sidebarService: SidebarService
  ) {
    this.quater = Math.floor((new Date().getMonth()) / 3);
    this.month = new Date().getMonth() - this.quater * 3;
    this.months = Array.apply(0, Array(3)).map((v, i: number) => moment().month(i + this.quater * 3).format('MMMM'));
    this.sidebarService.handleSetSettings({showSidebar: false, bgColor: '#fff'});
  }

  ngOnInit() {
    this.pages.push(this.route.snapshot.data.newEmployees);
    this.employees = this.route.snapshot.data.newEmployees.items;
    this.birthdays = this.getBirthdaysForEmployees(this.employees);
    this.hasNext = this.route.snapshot.data.newEmployees.hasNext;
  }

  private getMonthFirstDay(m: number) {
    return moment(new Date((new Date()).getFullYear(), m, 1));
  }

  private getMonthLastDay(m: number) {
    return moment(new Date((new Date()).getFullYear(), m, 1)).endOf('month');
  }

  public getBirthdaysPagetByMonth(month?: number) {
    this.month = month == null ? this.month : month ;
    this.index = 0;
    this.employees = [];
    this.pages = [];
    this.birthdays = [];
    this.hasPrev = false;
    this.hasNext = false;
    const first = this.getMonthFirstDay(this.month + this.quater * 3).toISOString();
    const last = this.getMonthLastDay(this.month + this.quater * 3).toISOString();
    this.employeesService.getItemsPaged({
        filter: `(slStartWorkDate ge datetime'${first}') and (slStartWorkDate le datetime'${last}')`,
        orderBy: [['slStartWorkDate', false]]
      }).subscribe((page: IListItemsPage<Employee>) => {
        this.employees = page.items;
        this.birthdays = this.getBirthdaysForEmployees(this.employees);
        this.hasNext = page.hasNext;
        this.pages.push(page);
      });
  }

  public getNext(): void {
    if (this.hasNext) {
      if (this.index === (this.pages.length - 1)) {
        this.pages[this.index].getNext()
          .subscribe((page: IListItemsPage<Employee>) => {
            this.index++;
            this.employees = page.items;
            this.birthdays = this.getBirthdaysForEmployees(page.items);
            this.hasNext = page.hasNext;
            this.hasPrev = true;
            this.pages.push(page);
          });
      } else {
        this.index++;
        this.employees = this.pages[this.index].items;
        this.birthdays = this.getBirthdaysForEmployees(this.employees);
        this.hasNext = this.pages[this.index].hasNext;
        this.hasPrev = true;
      }
    }
  }

  public getPrev(): void {
    if (this.hasPrev) {
      this.index--;
      this.employees = this.pages[this.index].items;
      this.birthdays = this.getBirthdaysForEmployees(this.employees);
      this.hasNext = true;
      this.hasPrev = this.index > 0;
    }
  }

  private getBirthdaysForEmployees(employees: Employee[]): Birthday[] {
    const birthdays: Birthday[] = [];
    const grouped = _.groupBy(employees, (employee: Employee) => employee.startdayTitleRelative);
    for (const group in grouped) {
      if (grouped.hasOwnProperty(group)) {
        const dates: string[] = group.split(' ');
        if (dates.length === 2) {
          birthdays.push({ birthday: dates[0], birthmonth: dates[1], employees: grouped[group] });
        }
      }
    }
    return birthdays;
  }
}
